<template>
  <div class="auth-page">
    <div class="auth-card">
      <div class="auth-header">
        <router-link to="/">
          <img src="@/assets/logo.svg" alt="AI2Cloth Logo" class="logo-img">
        </router-link>
        <h2>åˆ›å»ºä½ çš„è´¦æˆ·</h2>
        <p>åŠ å…¥ AI2Clothï¼Œå¼€å¯æ™ºèƒ½ç©¿æ­æ–°ä½“éªŒ</p>
      </div>
      <form @submit.prevent="handleRegister" class="auth-form">
        <div class="form-item" :class="{ error: errors.email }">
          <div class="input-wrapper">
            <span class="input-icon">ğŸ“§</span>
            <input 
              type="email" 
              v-model="registerForm.email" 
              placeholder="é‚®ç®±åœ°å€"
              class="form-input"
              @blur="validateEmail"
            >
          </div>
          <div v-if="errors.email" class="error-message">{{ errors.email }}</div>
        </div>
        
        <div class="form-item" :class="{ error: errors.password }">
          <div class="input-wrapper">
            <span class="input-icon">ğŸ”’</span>
            <input 
              :type="showPassword ? 'text' : 'password'" 
              v-model="registerForm.password" 
              placeholder="å¯†ç ï¼ˆè‡³å°‘6ä½ï¼‰"
              class="form-input"
              @blur="validatePassword"
            >
            <button 
              type="button" 
              class="password-toggle"
              @click="showPassword = !showPassword"
            >
              {{ showPassword ? 'ğŸ™ˆ' : 'ğŸ‘ï¸' }}
            </button>
          </div>
          <div v-if="errors.password" class="error-message">{{ errors.password }}</div>
        </div>
        
        <div class="form-item">
          <button 
            type="submit" 
            class="auth-button" 
            :disabled="loading"
            :class="{ loading: loading }"
          >
            <span v-if="loading" class="loading-spinner"></span>
            {{ loading ? 'æ³¨å†Œä¸­...' : 'ç«‹å³æ³¨å†Œ' }}
          </button>
        </div>
      </form>
      <div class="auth-footer">
        å·²æœ‰è´¦æˆ·ï¼Ÿ <router-link to="/login">ç«‹å³ç™»å½•</router-link>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @description ç”¨æˆ·æ³¨å†Œé¡µé¢
 */
import { mapActions } from 'vuex';

export default {
  name: 'Register',
  data() {
    return {
      loading: false,
      showPassword: false,
      registerForm: {
        email: '',
        password: ''
      },
      errors: {
        email: '',
        password: ''
      }
    };
  },
  methods: {
    ...mapActions('auth', ['register']),
    
    validateEmail() {
      const email = this.registerForm.email.trim();
      if (!email) {
        this.errors.email = 'è¯·è¾“å…¥é‚®ç®±åœ°å€';
        return false;
      }
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(email)) {
        this.errors.email = 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±åœ°å€';
        return false;
      }
      this.errors.email = '';
      return true;
    },
    
    validatePassword() {
      const password = this.registerForm.password;
      if (!password) {
        this.errors.password = 'è¯·è¾“å…¥å¯†ç ';
        return false;
      }
      if (password.length < 6) {
        this.errors.password = 'å¯†ç é•¿åº¦ä¸èƒ½å°‘äº 6 ä½';
        return false;
      }
      this.errors.password = '';
      return true;
    },
    
    validateForm() {
      const emailValid = this.validateEmail();
      const passwordValid = this.validatePassword();
      return emailValid && passwordValid;
    },
    
    async handleRegister() {
      if (!this.validateForm()) {
        return;
      }
      
      this.loading = true;
      try {
        await this.register(this.registerForm);
        this.$router.push('/dashboard');
        this.showMessage('æ³¨å†ŒæˆåŠŸï¼Œæ¬¢è¿åŠ å…¥ï¼', 'success');
      } catch (error) {
        const detail = error.response?.data?.detail || 'æ³¨å†Œå¤±è´¥ï¼Œè¯·ç¨åé‡è¯•ã€‚';
        this.showMessage(detail, 'error');
      } finally {
        this.loading = false;
      }
    },
    
    showMessage(message, type) {
      // åˆ›å»ºæ¶ˆæ¯æç¤ºå…ƒç´ 
      const messageEl = document.createElement('div');
      messageEl.className = `message-toast ${type}`;
      messageEl.textContent = message;
      
      // æ·»åŠ åˆ°é¡µé¢
      document.body.appendChild(messageEl);
      
      // æ˜¾ç¤ºåŠ¨ç”»
      setTimeout(() => {
        messageEl.classList.add('show');
      }, 100);
      
      // 3ç§’åç§»é™¤
      setTimeout(() => {
        messageEl.classList.remove('show');
        setTimeout(() => {
          document.body.removeChild(messageEl);
        }, 300);
      }, 3000);
    }
  }
};
</script>

