}

// è¾“å…¥æ¡†åŒ…è£…å™¨æ ·å¼
.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

// åŠ è½½çŠ¶æ€æ ·å¼
.auth-button {
  &.loading {
    cursor: not-allowed;
